import React, {useEffect, useState} from 'react';
import {useNavigate, useParams} from "react-router-dom";
import Axios from "axios";
import Modal from '../components/Modal';

function YangdoDetail(props) {

    // useState  open . (open true   )
    const [modalOpen, setModalOpen] = useState(false);

    const openModal = () => {
        setModalOpen(true);
    };
    const closeModal = () => {
        setModalOpen(false);
    };

    const [dto,setDto] = useState({});
    const {ynum, currentPage} = useParams();

    const navi = useNavigate();

    const [unum, setUnum]=useState(0);
    const unumchk=()=>{
        Axios.get("/login/unumChk?unum="+unum)
            .then(res=>{
                setUnum(res.data);
            })
    }
    useEffect(() => {
        unumchk()
    }, [])

    const selectData=()=>{
        const url = `/yangdo/detail?num=${ynum}`;
        Axios.get(url)
            .then(res=>{
                setDto(res.data);
            })
    }

    useEffect(()=>{
        selectData();
    },[]);

    return (
        <div>

            <React.Fragment>
                <Modal open={modalOpen} close={closeModal} header=" ">
                    <div>
                        <h5>[{dto.yplace}]</h5>
                        <p> : {dto.unickname}</p>
                        <p> : {dto.uhp}</p>
                        <p>
                                
                             .
                        </p>
                    </div>
                </Modal>
            </React.Fragment>

            <b> : {dto.yplace}</b><br/>
            <b> : {dto.unickname}</b><br/>
            <b> : {dto.ywriteday}</b><br/>
            <b> : {dto.yprice}</b><br/>
            <b>  : {dto.yday}</b><br/>
            <b>  : {dto.ysubject}</b><br/>
            <b>  : {dto.ycontent}</b><br/>

            <button type='button' onClick={()=>navi(`/yangdo/form`)}></button>
            <br/>
            <button type='button' onClick={()=>navi(`/yangdo/list/${currentPage}`)}></button>
            <br/>

            {
                unum !=null && unum==dto.unum?
                    <button type='button' onClick={()=>navi(`/yangdo/update/${dto.ynum}/${currentPage}`)}>
                        </button> :''
            }

            <br/>

            {
                unum !=null && unum==dto.unum?
                <button type='button'
                    onClick={()=>{
                        const url=`/yangdo/delete?num=${dto.ynum}`;
                        Axios.delete(url)
                        .then(res=>{
                            //  
                            alert(" / ?");
                            navi(`/yangdo/list/${currentPage}`);
                        })
                }}> / </button>:
                <button type='button' onClick={openModal}> </button>
            }
        </div>
    );
}

export default YangdoDetail;