import React, { Component } from 'react';
import Slider from 'react-slick';
import "./NoticeSlider.css";


export default class SimpleSlider extends Component {
    render() {
        const settings = {
            infinite: true,
            speed: 1000,
            slidesToShow: 1,
            slidesToScroll: 1,
            autoplay: true,
            autoplaySpeed: 5000,
            vertical: true, //   
        };
        return (
            <div className="notice_slider">
                <Slider {...settings}>
                    <div> 1.16.1 </div>
                    <div>3 1.16.1 </div>
                    <div> 1.16.1 </div>
                    <div> 1.16.1 </div>
                    <div> 1.16.1 </div>
                </Slider>
            </div>
        );
    }
}